<template>
  <div class="benefit_container">
      <head-top head-title="我的优惠券" goBack="true"></head-top>
      <section class="benefit_buju">
          <section class="benefit_xs">
              <div class="benfit_left_right">
                  <span :class="{'border-color':category==1}" @click="tihuan(1)">红包</span>
                  <span :class="{'border-color':category==2}"  @click="tihuan(2)">商家代金券</span>
              </div>
          </section>
         <section v-if="category==1">
             <section class="benefit_left" v-if="!showLoading">
                <section class="benefit_lr">
                    <span>有<span class="span_1">{{hongbaolist.length}}</span>个红包即将过期</span>
                    <div>
                        <img src="../../images/description.png" >
                        <router-link to="/benefit/hbDescription" class="balnce_sp_link">
                              红包说明
                        </router-link>
                    </div>
                </section>
                <section class="benefit_ul">
                    <!-- ../../images/hongbao.png -->
                    <li v-for="(item) in hongbaolist" :key="item.id">
                        <section class="hongbaolist_item">
                            <div class="hongbaolist_left">
                                <span>￥</span>
                                <span>{{String(item.amount).split('.')[0]}}.</span>
                                <span>{{String(item.amount).split('.')[1] || 0}}</span>
                                <p>{{item.description_map.sum_condition}}</p>
                            </div>
                            <div class="hongbaolist_right">
                                <div class="xs-left">
                                    <p>{{item.name}}</p>
                                    <p>{{item.description_map.validity_periods}}</p>
                                    <p>{{item.description_map.phone}}</p>
                                    <p v-if="item.limit_map">{{item.limit_map.restaurant_flavor_ids}}</p>
                                </div>
                                <div class="xs-right">
                                    <span>剩三日</span>
                                </div>
                            </div>
                        </section>
                    </li>
                </section>
             </section>
            <router-link class="lishi" to="/benefit/hbHistory">
                <div class="lishi_tx">
                    <span>查看历史红包</span>
                    <svg class="history_right">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                    </svg>
                </div>
            </router-link>
            <section class="benefit_top">
                <router-link class="left" to="/benefit/exchange">
                    <span> 兑换红包</span>
                </router-link>
                <router-link class="right" to="/benefit/commend">
                    <span>推荐有奖</span>
                </router-link>
            </section>
         </section>
        <section v-if="category==2">
             <section class="category2">
                 <div class="category_right">
                      <img src="../../images/description.png" >
                        <router-link to="/benefit/coupon" class="balnce_sp_link">
                            商家代金券说明
                        </router-link>
                 </div>
             </section>
             <section class="login_center">
                 <img src="../../images/voucher.png" height="170" width="300">
                 <p>无法使用代金券</p>
                 <p>非客户端或客户端版本低</p>
                 <router-link to="/download" tag="span">下载或升级客户端</router-link>
             </section>
        </section>
      </section>
      <transition name="benefit-view" mode="out-in">
          <router-view></router-view>
      </transition>
      <loading v-show="showLoading"></loading>
  </div>
</template>

<script>
import headTop from '../../components/header/header'
import {mapState,mapMutations} from 'vuex'
import loading from '../../components/common/loading'
import {getHongbaoNum} from '../../service/getData'
export default {
    components: {
        headTop,
        loading
    },
    data()
    {
        return{
            showAlert:false,
            alertText:null,
            showLoading:true,
            hongbaolist:null,//红包列表
            category:1
        }
    },computed: {
        ...mapState([
            'userInfo'
        ])
    },methods: {
        ...mapMutations([
            'CLEAR_CART'
        ]),
        //初始化获取红白
        async initData()
        {
            this.showLoading=true;
            if(this.userInfo&&this.userInfo.user_id)
            {
                 let res=await getHongbaoNum(this.userInfo.user_id)
                 this.hongbaolist=res;
                 this.showLoading=false;
                 //console.log(JSON.stringify(res));
                 
            }
        },
        //控制红包和代金券的显示隐藏
        tihuan(id)
        {
            this.category=id;
        }
    },created () {
        this.initData();
    },watch: {
        userInfo:function(value)
        {
            this.initData();
        }
    }
}
</script>

<style scopde lang="scss">
@import '../../style/mixin.scss';
.benefit_buju{
            p, span{
            font-family: Helvetica Neue,Tahoma,Arial;
        }
}
.benefit_xs{
    background: #fff;
    margin-top:2rem;
    .benfit_left_right{
        padding:0.4rem;
        display: flex;
        justify-content: space-around;
        span{
            font-size: 0.6rem;
        }
    }
}
.border-color{
    border-bottom: 2px solid $blue;
    padding-bottom: 0.2rem;
    color:$blue;
    
}
.benefit_left{
    padding:0.7rem;
    .benefit_lr{
        display: flex;
        justify-content: space-between;
        align-items: center;
        span{
            @include sc(0.5rem,#333);
        }
        .span_1
        {
            color:#f60;
            font-size: 0.4rem;
        }
        div{
            display: flex;
            align-items: center;
            img{
                @include wh(0.6rem,0.6rem);
                margin-right: 0.3rem;
            }
            .balnce_sp_link{
                @include sc(0.5rem,#333);
                color:$blue;
            }
        }
    }
}
.benefit_ul{
    margin-top:0.4rem;

    li{
        background: #fff url(../../images/hongbao.png) repeat-x;
        margin-top:0.8rem;
        padding:1rem 0;
        border:0.025rem solid #ddd;
        border-radius: 0.3rem;
        .hongbaolist_item{
            display: flex;
            .hongbaolist_left{
                border-right: 0.025rem solid #ddd;
                padding-left:0.4rem;
                flex:1;
                font-size: 0;
                span{
                    
                    color:#f60;
                    font-size: 0.8rem;
                }
                span:nth-of-type(1)
                {
                    font-weight: bold;
                }
                span:nth-of-type(2)
                {
                    font-size: 1.5rem;
                    font-weight: 500;
                    margin-left:0.1rem;
                }
                p{
                    @include sc(0.2rem,#d1d1d1);
                    
                }
            }
            .hongbaolist_right{
                flex:3;
                display: flex;
                justify-content: space-between;
                .xs-left{
                    padding-left:0.3rem;
                    p:nth-of-type(1)
                    {
                        @include sc(0.7rem,#333);
                        font-weight: 300;
                    }
                    p:nth-of-type(2)
                    {
                        @include sc(0.5rem,#d1d1d1);
                    }
                    p:nth-of-type(3)
                    {
                        @include sc(0.5rem,#d1d1d1);
                    }
                    p{
                        padding-bottom: 0.2rem;
                    }
                    p:nth-of-type(4)
                    {
                        @include sc(0.5rem,#f60);
                    }
                }
                .xs-right{
                    span{
                        color:#f60;
                        font-size: 0.7rem;
                        font-weight: bold;
                        padding-right: 0.5rem;
                    }
                }
            }
        }
        .list_item_footer{
            p{
                @include sc(0.6rem,#333);
            }
        }
    }
}
.benefit_top{
    position: fixed;
    background: #fff;
   bottom: 0;
   display: flex;
   width: 100%;
  padding:0.5rem;
  align-items: center;
  border:1px solid #ddd;
  text-align: center;
    .left{
        @include sc(0.7rem,#555);
        font-weight: 400;
       flex:1;
       border-right: 1px solid #ddd;
       
    }
    .right{
        @include sc(0.7rem,#555);
        font-weight: 400;
        flex:1;
    }
}
.category2{
    margin-top: 0.5rem;
    margin-right:0.3rem;
    .category_right{
        display: flex;
        justify-content: flex-end;
        align-items: center;
        img{
            @include wh(.6rem,.6rem);
            padding-right: 0.3rem;
        }
        .balnce_sp_link{
            @include sc(0.5rem,$blue);
        }
    }
}
.login_center{
    text-align: center;
    margin-top: 6rem;
    img{
        @include wh(8rem,5rem);
    }
    p{
        @include sc(0.5rem,#999);
        padding-bottom: 0.4rem;
    }
    span{
        background: #56d176;
        @include sc(0.6rem,#fff);
        font-weight: bold;
        padding:0.5rem;
       border-radius: 0.2rem;
    }
}
    .lishi{
        .lishi_tx{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 2.8rem;
            text-align: center;
           span{
                @include sc(0.5rem,#d2d2d2);
           }

            svg{
                display: flex;
                align-items: center;
                @include wh(.4rem,.4rem);
                fill: #d2d2d2;
                padding-left:0.3rem;
            }
        }
    }
.benefit-view-enter-active,.benefit-view-leave-active{
    transition: all .4s;
}
.benefit-view-enter,.benefit-view-leave-active{
    transform: translate3d(2rem,0,0);
    opacity: 0;
}
</style>



